import React, { useState } from "react";
import StyledList from "./../components/shared/StyledList/StyledList";
import CompanyPresentation from "../components/CompanyPresentation/CompanyPresentation";
import ImageTextBlock from "./../components/ImageTextBlock/ImageTextBlock";
import CvForm from './../components/CvForm/'
import TeamSlider from './../components/TeamSlider/TeamSlider';
import SubscribeBlock from "./../components/SubscribeBlock/SubscribeBlock";
import ScrollToTop from './../components/shared/ScrollToTop'
import ScrollAnimation from 'react-animate-on-scroll'

export default function WhyMarksem(props) {
  useState(() => {
    window.scrollTo(0, 0);
  })

  return (
    <div>
      <ScrollToTop />
      <ScrollAnimation
        animateIn="fadeIn"
        animateOnce={true}>
        <CompanyPresentation
          action={true}
          title={
            <h1 className="company-presentation__title title--under"> MARKSEM</h1>
          }
          info={
            <div className="company-presentation__info" style={{maxWidth: "530px"}}>
              <p className="f21" style={{fontWeight: "500"}}>
                     ,      ,      !
              </p>
              <p style={{fontWeight: "normal", lineHeight: "27px", paddingTop: "24px"}}>
                 ,          .        .   ,         ,        .
              </p>
            </div>
          }
          img="./img/why-marksem-top.png"
        />
      </ScrollAnimation>


      <ScrollAnimation
        animateIn="fadeIn"
        animateOnce={true}>
        <ImageTextBlock
          type="1"
          title=" "
          mediaType="img"
          isBackground={true}
          mediaPosition="left"
          imgUrl="./img/our_values.png"
          actionOnClick={() => console.log("click")}
          actionElementType="link"
          actionElementText="Watch the video about us"
          actionElementAlign="right"
        >
          <p>.    .    ,         .</p>
          <p>.    ,     .        .</p>
          <p>.    , ,   .</p>
          <p>.       ,     .         .         .</p>
        </ImageTextBlock>
      </ScrollAnimation>


      <ScrollAnimation
        animateIn="fadeIn"
        animateOnce={true}>
        <ImageTextBlock
          type="2"
          title=" MARKSEM"
          mediaType="img"
          mediaPosition="right"
          imgUrl="./img/investment-metrics.png"
          actionOnClick={() => console.log("click")}
          actionElementType="button"
          actionElementText="Invest now"
          actionElementAlign="left"
        >
          <StyledList
            iconUrl="./icons-sprite.svg#investment-block-icon"
            iconSize={37}
            alignIcon="center"
            items={[
              {
                text: "     "
              },
              {
                text: "     "
              },
              {
                text: "    "
              }
            ]}
          />
        </ImageTextBlock>
      </ScrollAnimation>

      <ScrollAnimation
        animateIn="fadeIn"
        animateOnce={true}>
        <ImageTextBlock
          type="1"
          title=" "
          mediaType="img"
          mediaPosition="left"
          imgUrl="./img/terms_of_cooperation.png"
          actionOnClick={() => console.log("click")}
          actionElementType="button"
          actionElementText="Contact us"
          actionElementAlign="center"
        >
          <p>              .</p>
          <p>       MARKSEM.</p>
          <p> ,   .</p>
        </ImageTextBlock>
      </ScrollAnimation>

      <ScrollAnimation
        animateIn="fadeIn"
        animateOnce={true}>
        <TeamSlider
          title="Meet our team"
          secondaryText="We are the best team"
          teamMembers={[
            {
              imgUrl: "./img/team1.png",
              name: "Fedir Drozdov",
              role: "Co-founder",
              facebookLink: "#",
              linkdInLink: "#"
            },
            {
              imgUrl: "./img/team2.png",
              name: "Fedir Drozdov",
              role: "Co-founder",
              facebookLink: "#",
              linkdInLink: "#",
              instagramLink: "#"
            },
            {
              imgUrl: "./img/team3.png",
              name: "Fedir Drozdov",
              role: "Co-founder",
              facebookLink: "#",
              instagramLink: "#"
            }
          ]}
        />
      </ScrollAnimation>


      <ScrollAnimation
        animateIn="fadeIn"
        animateOnce={true}>
        <CvForm />
      </ScrollAnimation>

      <SubscribeBlock type='minimal' />

    </div>
  );
}